.Post {
    background: white;
    padding: 1rem;
    border-radius: 1rem;
}
.Post  img {

border-radius: 1rem;

}
.iconReact  img {
 width: 40px;
 object-fit: contain;

}
.Post {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.iconReact svg {
    font-size: 1.6rem;
}
.iconReact{
    display: flex;
    gap: 1rem;
}
.user-posting {
    display: flex;
    flex-direction: row;
    gap: .6rem;
    align-items: center;
}
.user-posting img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    object-fit: cover;
}
.posting-img {
    display: grid;
    grid-template-columns: 1fr .5fr;
    grid-template-rows: 1fr;
    gap: .6rem;
}
.posting-img>img {
    height: 100%;
}
.more-img {
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: .6rem;
    position: relative;
    
}
.more-img-count {
    background-color: rgba(0, 0, 0, 0.69);
    position: absolute;
    bottom: 0;
border-radius: 1rem;

    width: 100%;
    height: 49%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}
.iconReact img{
    border-radius: 0;
}
.iconReact h1{
    display: flex;
    flex-direction: row;
    position: relative;
margin-bottom: 1rem;
    gap: .5rem;
}
.iconReact h1>img{
    width: 25px;
}
.emotion {
    visibility: hidden;
    position: absolute;
    display: flex;
    background: white;
    border-radius: 2rem;
    top: 1rem;
    gap: .3rem;
    box-shadow: 1px 1px 5px rgb(79, 79, 79);
    padding:.3rem .7rem;
}
.emotion img{
    width: 20px;
}
.emotion img:hover {
    transform: scale(1.5);
    transition: all .2s linear ;
}
.iconReact h1:hover .emotion {
    visibility: visible;

}