.container {
    display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: .6rem;
}
.story img{
    width: 100%;
    border-radius: 1rem;
}
.story {
    position: relative;
}
.Story h1 {
    margin-bottom: 1rem;
    color: var(--orange);
}

.story-avatar {
    background-color: rgba(59, 58, 58, 0.229);
    width: 100%;
    border-radius:0 0 1rem 1rem;
    bottom: 4px;
    display: flex;
    /* align-items: to; */
    justify-content: center;
    height: 30%;
    /* align-items: flex-start; */
    
    backdrop-filter: blur(10px);
  
    position: absolute;
}
.story-avatar img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    object-fit: cover;
  top: -1rem;
  box-shadow: 1px 1px 5px rgb(31, 31, 31);
    position: absolute;
}
